  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Updated */
  height: 100vh;
  background-color: #f7f7f7;
}

.registration-form,
.signin-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  margin-top: 2rem; /* Added */
}

input {
  padding: 0.5rem;
  width: 300px;
  border: none;
  border-radius: 5px;
