/* welcome page */

.welcome-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-align: center;
}

.welcome-container h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.welcome-description {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.features-section {
  margin-bottom: 30px;
  text-align: left;
}

.features-section h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.features-list {
  list-style-type: disc;
  padding-left: 20px;
}

.cta-section {
  margin-bottom: 30px;
}

.cta-section h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.cta-section p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.cta-button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #2196f3;
  color: #fff;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #1976d2;
}

.login-section {
  margin-bottom: 30px;
}

.login-link {
  color: #2196f3;
  text-decoration: none;
}

.current-time, .user-location, .window-size {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.current-time button {
  margin-top: 10px;
  padding: 5px 10px;
  background-color: #2196f3;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.current-time button:hover {
  background-color: #1976d2;
}

@media (max-width: 600px) {
  .welcome-container h2 {
    font-size: 2rem;
  }

  .welcome-description {
    font-size: 1rem;
  }

  .features-section h3 {
    font-size: 1.3rem;
  }

  .features-list {
    font-size: 1rem;
  }

  .cta-section h3 {
    font-size: 1.3rem;
  }

  .cta-section p {
    font-size: 1rem;
  }

  .current-time, .user-location, .window-size {
    font-size: 1rem;
  }

  .current-time button {
    font-size: 0.9rem;
  }
}


/* Individual Feature Page Styles */
.feature-page {
  text-align: center;
  padding: 20px;
}

.feature-page h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.feature-page p {
